<template>
    <div class="trend-box">
        <div class="top-image">
            <van-image
                    width="100%"
                    height="100%"
                    :src="trend_top_src"
                    fit="fill"
            >
                <template v-slot:error>加载失败</template>
                <template v-slot:loading>
                    <van-loading type="spinner" size="20"></van-loading>
                </template>
            </van-image>
        </div>
        <div class="info-box">
            <div class="info-top">
                <div class="remain-box">
                    <div>{{remain}}</div>
                    <div class="tips-box">可用</div>
                </div>
                <div class="middle-line van-hairline--left"></div>
                <div class="has-pay">
                    <div>{{haspay}}</div>
                    <div class="tips-box">已投资</div>
                </div>
            </div>
            <div class="van-hairline--top" style="height: 1px;width: 100%;"></div>
            <van-field
                    label="投资"
                    v-model="paynum"
                    input-align="right"
                    disabled
            ></van-field>
            <van-field
                    label="收益"
                    v-model="getnum"
                    input-align="right"
                    disabled
            ></van-field>
            <van-field
                    label="提现"
                    v-model="tip_text"
                    input-align="right"
                    disabled
            ></van-field>
            <div class="remark-box">{{remark}}</div>
        </div>

        <van-button type="info" style="width: 85vw;margin-top: 10vw;" @click="show_key_board=true">确认投资</van-button>

        <van-popup
                v-model="show_key_board"
                position="bottom"
                fixed
                :style="{width: '100vw', 'min-height':'40vw', 'z-index':3000}"
                get-container="#app"
        >

            <van-nav-bar
                    left-arrow
                    title="交易密码"
                    @click-left="onClickLeft"
            ></van-nav-bar>
            <!-- 密码输入框 -->
            <div style="width: 100%; height: 14vw;">
                <van-password-input :value="password"/>
            </div>
            <!-- 数字键盘 -->
            <div style="height: 55vw;">
                <van-number-keyboard
                        :show="true"
                        @input="onInput"
                        @delete="onDelete"
                />
            </div>
        </van-popup>
    </div>
</template>

<script>
    import { Image, Loading, Field, Button, Popup, PasswordInput, NumberKeyboard, NavBar } from 'vant'
    export default {
        name: 'trend',
        components: {
            [Image.name]: Image,
            [Loading.name]: Loading,
            [Field.name]: Field,
            [Button.name]: Button,
            [Popup.name]: Popup,
            [PasswordInput.name]: PasswordInput,
            [NumberKeyboard.name]: NumberKeyboard,
            [NavBar.name]: NavBar,
        },
        data(){
            return {
                show_key_board: false,
                password: '',

                trend_top_src: 'https://img.yzcdn.cn/vant/cat.jpeg',
                remain: '0.00',
                haspay: '0.00',
                paynum: '2000 QC',
                getnum: '140 QC',
                tip_text: 'T+0到账',
                remark: '注：每天可以排1单 一周可以排7单',
            }
        },
        created() {
            this.$parent.active = 1;
        },
        methods: {
            onClickLeft () {
                this.show_key_board = false;
                this.password = '';
            },
            onInput (key) {
                this.password = (this.password + key).slice(0, 6);
            },
            onDelete () {
                this.password = this.password.slice(0, this.password.length - 1);
            },
        }

    }
</script>

